.ControlPoint {
	width: 2em;
	height: 2em;
	border-radius: 50%;
	border: 2px solid #09c;
	background-color: #2be;
	cursor: -webkit-grab;
	cursor: grab;
	-webkit-transform: translate(-1em, -1em);
	    -ms-transform: translate(-1em, -1em);
	        transform: translate(-1em, -1em);
}



